@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: local('inter'),
    url('./inter/regular.woff2') format('woff2'),
    url('./inter/regular.woff') format('woff'); 
}

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  src: local('inter'),
    url('./inter/medium.woff2') format('woff2'),
    url('./inter/mediumItalic.woff') format('woff'); 
}

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 900;
  src: local('inter'),
  url('./inter/black.woff2') format('woff2'),
  url('./inter/black.woff') format('woff');
}
